
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import { getYMDFormateTime, isArrayType } from "../../../js/utility.js"
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapState } from 'vuex'

export default {
name: 'examinemain',
components: {
Grid,
GridItem,
GroupTitle,
Badge
},
computed: {
...mapState([
'examineMain'
]),
},
data() {
return {
itemlist: [],
create_count: '0',
examine_count: '0',
allItems: [{
title: '普通审批',
path: 'examinehandle/1/add/0',
url: require('../../../assets/general_examine.png')
}, {
title: '请假审批',
path: 'examinehandle/2/add/0',
url: require('../../../assets/leave_examine.png')
}, {
title: '普通报销',
path: 'examinehandle/3/add/0',
url: require('../../../assets/general_apply.png')
}, {
title: '差旅报销',
path: 'examinehandle/4/add/0',
url: require('../../../assets/travel_apply.png')
}, {
title: '出差申请',
path: 'examinehandle/5/add/0',
url: require('../../../assets/travel_examine.png')
}, {
title: '借款申请',
path: 'examinehandle/6/add/0',
url: require('../../../assets/loan_examine.png')
}],
}
},
beforeRouteLeave(to, from, next) {
if (to.name === 'office') { //退出清除审批列表缓存
this.$store.commit('updateExamineList', {});
this.$store.commit('updateExamineMain', {});
} else {
this.$store.commit('updateExamineMain', {
type: 'examinemain',
itemlist: this.itemlist,
create_count: this.create_count,
examine_count: this.examine_count
});
}


next();
},
mounted() {

if (this.examineMain.type === 'examinemain') {
this.itemlist = this.examineMain.itemlist;
this.create_count = this.examineMain.create_count;
this.examine_count = this.examineMain.examine_count;
this.getSilentDynamic();
} else {
this.getDynamic();
}
},
methods: {
getSilentDynamic() {
AjaxSilentHttp('POST', 'm=examine&a=dynamic', {}, this, function(res, wself, success) {
if (success) {
wself.create_count = res.data.create_count;
wself.examine_count = res.data.examine_count;
wself.itemlist = [];
if (res.data.permission_list.add === 1) {
var type_list = res.data.type_list;
for (var i = 0; i < type_list.length; i++) {
var item = type_list[i];
wself.itemlist.push(wself.allItems[parseInt(item) - 1]);
}
} else {
wself.itemlist = [];
}
}
});
},
getDynamic() {
AjaxHttp('POST', 'm=examine&a=dynamic', {}, this, function(res, wself, success) {
if (success) {
wself.create_count = res.data.create_count;
wself.examine_count = res.data.examine_count;
if (res.data.permission_list.add === 1) {
var type_list = res.data.type_list;
for (var i = 0; i < type_list.length; i++) {
var item = type_list[i];
wself.itemlist.push(wself.allItems[parseInt(item) - 1]);
}
} else {
wself.itemlist = [];
}
}
});
},
enterList(type) {
if (type === '1') {
this.$router.push({
name: 'examinelist',
});
} else if (type === '2') {
this.$router.push({
name: 'examinemylist',
});
}

}
}
}




